:root {
  --background: rgb(43, 44, 44);
  --text: #fafafa;
}

.news {
  color: var(--background);
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
}

.news > h2 {
  font-size: 3rem;
  margin-bottom: 20px;
  text-align: center;
}
.news > h3 {
  font-size: 2rem;
  margin-right: auto;
}
.news > p + h3 {
  margin-top: 1rem;
}

.news > p {
  font-size: 1.6rem;
}

.news a {
  text-decoration: underline;
}

@media screen and (min-width: 768px) {
  .news {
    padding-bottom: 4rem;
  }
}
